<template>
    <div id="userAdministration">
        <div class="view_main">
            <el-input
            placeholder=""
            class="search_input"
            v-model="searchText">
            </el-input>
            <el-button></el-button>
            <el-button></el-button>
        </div>
        <div class="view_main">
            <template>
                <el-table
                :data="tableData"
                border
                style="width: 100%">
                    <el-table-column
                    fixed
                    prop="id"
                    label="ID"
                    >
                    </el-table-column>
                    <el-table-column
                    prop="name"
                    label=""
                    >
                    </el-table-column>
                    <el-table-column
                    label=""
                    >
                        <template slot-scope="scope">
                            <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
                            <el-button type="text" size="small"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </template>
        </div>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                tableData: [{
                    id: '2016-05-03',
                    name: '',
                    province: '',
                    city: '',
                    address: ' 1518 ',
                    zip: 200333
                }],
                searchText:""
            }
        }
    }
</script>
<style>
.search_input{
    width: 300px;
}
.view_main{
    overflow:hidden;
    margin-top:10px
}
</style>


